body {
  background: linear-gradient(#e66465, #9198e5) fixed;
  font-family: Roboto, sans-serif;
}

.search-form {
  text-align: center;
  background-color: #000;
  padding: 20px 0;
  transition: background-color .3s;
}

.search-form:focus, .search-form input[type="text"]:focus {
  outline: none;
}

.search-form input[type="text"] {
  width: 300px;
  color: #fff;
  background-color: rgba(0, 0, 0, 0);
  border: none;
  border-bottom: 2px solid #fff;
  margin-right: 10px;
  padding: 10px;
  transition: border-color .3s;
}

.search-form button {
  color: #fff;
  cursor: pointer;
  background-color: #000;
  border: none;
  padding: 10px 20px;
  transition: background-color .3s, color .3s;
}

.search-form input[type="text"]:hover {
  border-color: red;
}

.search-form button:hover {
  color: #000;
  background-color: red;
}

.photo-card {
  cursor: pointer;
  width: 300px;
  vertical-align: top;
  background-color: #000;
  border-radius: 0;
  margin: 10px;
  transition: transform .3s ease-in-out;
  display: inline-block;
  overflow: hidden;
  box-shadow: 0 0 5px rgba(0, 0, 0, .5);
}

.photo-card:hover {
  transform: scale(1.05);
}

.photo-card img {
  width: 100%;
  height: 200px;
  display: block;
}

.info {
  color: #fff;
  background-color: #000;
  padding: 10px;
}

.info p {
  margin: 0;
  font-size: 14px;
}

.info-item {
  justify-content: space-between;
  align-items: center;
  margin-bottom: 8px;
  display: flex;
}

.info-item b {
  margin-right: 5px;
  font-weight: bold;
}

.load-more {
  color: #fff;
  cursor: pointer;
  background-color: #000;
  border: none;
  margin-top: 20px;
  margin-bottom: 20px;
  margin-left: 20px;
  padding: 10px 20px;
  transition: background-color .3s, color .3s;
}

.load-more:hover {
  color: #000;
  background-color: red;
}

.gallery {
  flex-wrap: wrap;
  justify-content: center;
  gap: 1rem;
  margin-bottom: 20px;
  display: flex;
}

